.stats {
    display: flex;
    justify-content: space-around;
    padding: 0;
    margin: 0;
    list-style-type: none;
    background-color: rgb(245, 246, 245);
    border-radius: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.22;
}

.stats_title {
    width: 100%;
    border: 1px solid rgb(124, 124, 119);
    text-align: center;
    padding: 16px;
    background-color: #f1f1f5;
}

.stats_title:first-child {
    border-bottom-left-radius: 10px;
}

.stats_title:last-child {
    border-bottom-right-radius: 10px;
}

.label {
    display: block;
    flex-direction: column;    
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.22;
    color:rgb(124, 124, 119);
}

.quantity {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.22;
}